// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'
import VueAxios from 'vue-axios'
import wx from 'weixin-js-sdk'
import querystring from 'querystring'
import store from './vuex/store'
import api from '@/config/api'
import { setCookie, getCookie, deleteCookie } from '@/assets/js/cookieHandle.js'
import { isWeixn } from '@/assets/js/wxHandle.js'

import { Button, Loadmore, Lazyload, Popup, Field, DatetimePicker, Picker, Actionsheet, Radio, Toast, Indicator, Spinner } from 'mint-ui'
import 'mint-ui/lib/style.css'

// VideoPlayer.config({
//   youtube: true,
//   switcher: true,
//   hls: true
// })

Vue.component(Button.name, Button)
Vue.component(Loadmore.name, Loadmore)
Vue.component(Popup.name, Popup)
Vue.component(Field.name, Field)
Vue.component(DatetimePicker.name, DatetimePicker)
Vue.component(Picker.name, Picker)
Vue.component(Actionsheet.name, Actionsheet) 
Vue.component(Radio.name, Radio)
Vue.component(Spinner.name, Spinner)

Vue.use(VueAxios, axios)
Vue.use(Lazyload, {
    loading: require('@/assets/img/img-loading.png')
})

Vue.config.productionTip = false;
Vue.prototype.api = api;
Vue.prototype.Toast = Toast;
Vue.prototype.Indicator = Indicator;

// 
Vue.prototype.wxShare = function (options) {

    var op = { url: '', title: '', desc: '', link: '', imgUrl: '' };
    options = $.extend({}, op, options);

    // var link  = encodeURIComponent(link);
    // const url = encodeURIComponent(location.href.split('#')[0]);// url
    // const url = encodeURIComponent(window.location.href.split('#')[0]);
    // const url = location.href;
    axios.post(api.shareSign(), querystring.stringify({
        url: options.url
    })).then(res => {
        var Data = res.data.sign;
        // configreadyconfigconfig
        wx.config({
            debug: false, // ,apialertpclogpc
            appId: Data.appId,   // 
            timestamp: Data.timestamp, // 
            nonceStr: Data.nonceStr,   // 
            signature: Data.signature, // 1
            jsApiList: ['onMenuShareAppMessage', 'onMenuShareTimeline'] // JSJS2
        });
    });
    wx.ready(() => {
        // ready
        // ready
        wx.onMenuShareAppMessage({ // 
            title: options.title, // 
            desc: options.desc,   // 
            link: options.link,   //  
            imgUrl: options.imgUrl, // 
            // 
            success: function () {

            },
            // 
            cancel: function () {
                console.log('');
            }
        });
        //
        wx.onMenuShareTimeline({
            title: options.title, // 
            desc: options.desc,
            link: options.link,
            imgUrl: options.imgUrl,
            // 
            success: function () {

            },
            // 
            cancel: function () {
                console.log('');
            }
        });
    });
};


router.beforeEach((to, from, next) => {
    $("input").blur(); // input
    document.title = to.meta.title;
    store.dispatch('showHeadBar', to.meta.showHeadBar);
    store.dispatch('showFooterBar', to.meta.showFooterBar);
    if (process.env.NODE_ENV == 'production') {
        if (!to.meta.requireAuth) {
            axios.get(api.isLogin()).then(res => {
                let dataUser = res.data;
                if (res.data.status == 0) {
                    // 
                    console.log('')
                    if (isWeixn() && store.state.appInit.has_wxgzh_login) {
                        // 
                        axios.post(api.wxLogin() + '&redirect=' + encodeURIComponent(document.URL)).then(res => {
                            var data = res.data
                            window.location.href = data.url;
                        });
                    }
                    else {
                        next({
                            path: '/login/index',
                            query: { redirect: to.fullPath } //
                        });
                    }
                    // next({
                    //     path: '/login/index',
                    //     query: { redirect: to.fullPath } //
                    // });
                }
                else if (!store.state.userInfo || !store.state.appInit) {
                    // vuex
                    if (!store.state.userInfo && !store.state.appInit) {
                        axios.all([axios.get(api.getUserHome()), axios.get(api.getAppInit())])
                            .then(axios.spread(function (userInfo, appInit) {
                                store.dispatch('setUserInfo', userInfo.data.user);
                                store.dispatch('setAppInit', appInit.data);
                                next();
                            }));
                    }
                    else {
                        if (!store.state.userInfo) {
                            axios.get(api.getUserHome()).then(res => {
                                store.dispatch('setUserInfo', res.data.user);
                                next();
                            });
                        }
                        else if (!store.state.appInit) {
                            axios.get(api.getAppInit()).then(res => {
                                store.dispatch('setAppInit', res.data);
                                next();
                            });
                        }
                    }
                }
                else {
                    // 
                    next();
                }
            });
        }
        else {
            if (!store.state.userInfo && !store.state.appInit) {
                axios.all([axios.get(api.getUserHome()), axios.get(api.getAppInit())])
                    .then(axios.spread(function (userInfo, appInit) {
                        store.dispatch('setUserInfo', userInfo.data.user);
                        store.dispatch('setAppInit', appInit.data);
                        next();
                    }));
            }
            else {
                if (!store.state.userInfo) {
                    axios.get(api.getUserHome()).then(res => {
                        store.dispatch('setUserInfo', res.data.user);
                        next();
                    });
                }
                else if (!store.state.appInit) {
                    axios.get(api.getAppInit()).then(res => {
                        store.dispatch('setAppInit', res.data);
                        next();
                    });
                }
                else {
                    next();
                }
            }
        }
    }
    else {
        if (!store.state.userInfo || !store.state.appInit) {
            if (!store.state.userInfo && !store.state.appInit) {
                axios.all([axios.get('/gm/api/user_home'), axios.get('/gm/api/appInit')])
                    .then(axios.spread(function (userInfo, appInit) {
                        store.dispatch('setUserInfo', userInfo.data.user);
                        store.dispatch('setAppInit', appInit.data);
                        next();
                    }));
            }
            else {
                if (!store.state.userInfo) {
                    axios.get('/gm/api/user_home').then(res => {
                        store.dispatch('setUserInfo', res.data.user);
                        next();
                    });
                }
                if (!store.state.appInit) {
                    axios.get('/gm/api/appInit').then(res => {
                        store.dispatch('setAppInit', res.data);
                        next();
                    });
                }
            }
        }
        else {
            next();
        }
    }
});


/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store,
    template: '<App/>',
    components: { App }
})



