import { Emotions } from '@/assets/js/emotions.js'; // Emotions

var curPlayAudio = null;
function onChangePlayAudio(playAudio) {
    if (curPlayAudio) {
        if (curPlayAudio != playAudio) {
            curPlayAudio.currentTime = 0;
            curPlayAudio.pause();
            curPlayAudio = playAudio;
        }
    } else {
        curPlayAudio = playAudio;
    }
}

//
function imageClick(imgObj) {
    var imgUrls = imgObj.src;
    var imgUrlArr = imgUrls.split("#"); //
    var smallImgUrl = imgUrlArr[0];//
    var bigImgUrl = imgUrlArr[1];//
    var oriImgUrl = imgUrlArr[2];//
    var html = '<img class="img-thumbnail" src="' + bigImgUrl + '" />';
    $.weeboxs.open(html, {animate:false, showButton:false, showCancel:false, showOk:false, modal: true, showTitle: false, clickClose: true});
}

function convertMsg(msg, loginInfo, vueObj) {
    return convertMsgtoHtml(msg, loginInfo, vueObj);
}

function convertMsgtoHtml(msg, loginInfo, vueObj) {
    var html = "", elems, elem, type, content;
    elems = msg.getElems();//
    for (var i in elems) {
        if(elems.hasOwnProperty(i)){
            elem = elems[i];
            type = elem.getType();//
            content = elem.getContent();//
            switch (type) {
                case webim.MSG_ELEMENT_TYPE.TEXT:
                    var text = content.getText();
                    if(typeof html == 'object'){
                        html.text = text;
                    } else {
                        html += text;
                    }
                    break;
                case webim.MSG_ELEMENT_TYPE.FACE:
                    html += convertFaceMsgToHtml(content);
                    break;
                case webim.MSG_ELEMENT_TYPE.IMAGE:
                    return convertImageMsgToHtml(content);
                case webim.MSG_ELEMENT_TYPE.SOUND:
                    return convertSoundMsgToHtml(content);
                case webim.MSG_ELEMENT_TYPE.FILE:
                    html += convertFileMsgToHtml(content);
                    break;
                case webim.MSG_ELEMENT_TYPE.LOCATION://
                    //html += convertLocationMsgToHtml(content);
                    break;
                case webim.MSG_ELEMENT_TYPE.CUSTOM:
                    html = convertCustomMsgToHtml(content, loginInfo, vueObj);
                    break;
                case webim.MSG_ELEMENT_TYPE.GROUP_TIP:
                    // html += convertGroupTipMsgToHtml(content);
                    break;
                default:
                    webim.Log.error(': elemType=' + type);
                    break;
            }
        }
        
    }
    return html;
}

//
function convertSoundMsgToHtml(content) {
    var second = content.getSecond();//
    var downUrl = content.getDownUrl();
    if (webim.BROWSER_INFO.type == 'ie' && parseInt(webim.BROWSER_INFO.ver) <= 8) {
        return '[]demoie8(),URL:' + downUrl;
    }
    return '<audio src="' + downUrl + '" controls="controls" onplay="onChangePlayAudio(this)" preload="none"></audio>';
}

//
function convertImageMsgToHtml(content) {
    var smallImage = content.getImage(webim.IMAGE_TYPE.SMALL);//
    var bigImage = content.getImage(webim.IMAGE_TYPE.LARGE);//
    var oriImage = content.getImage(webim.IMAGE_TYPE.ORIGIN);//
    if (!bigImage) {
        bigImage = smallImage;
    }
    if (!oriImage) {
        oriImage = smallImage;
    }
    return	"<img src='" + smallImage.getUrl() + "#" + bigImage.getUrl() + "#" + oriImage.getUrl() + "' style='cursor: pointer;' id='" + content.getImageId() + "' bigImgUrl='" + bigImage.getUrl() + "' onclick='imageClick(this)' />";
}

function convertCustomMsgToHtml(content, loginInfo, vueObj) {
    var data = content.getData();//
    var desc = content.getDesc();//

    var msg = JSON.parse(data);

    var user_level, nick_name, text;
    switch (msg.type) {
        case 0: // 
            user_level = msg.sender.user_level;
            nick_name = msg.sender.nick_name;
            text = msg.text;
            break;
        case 1: // 
            user_level = msg.sender.user_level;
            nick_name = msg.sender.nick_name;
            var icon = msg.pc_icon || msg.icon;
            text = msg.desc + '&nbsp;<img class="msg-icon" style="height: 1rem;" src="' + icon + '" />';
            break;
        case 2: // 
            user_level = msg.sender.user_level;
            nick_name = msg.sender.nick_name;
            text = msg.desc || msg.text;
            break;
        case 3: // 
            user_level = msg.sender.user_level;
            nick_name = msg.sender.nick_name;
            text = '';
            break;
        case 5: // 
            if(typeof msg.sender.nick_name === 'undefined'){
                return;
            }
            user_level = 99;
            nick_name = '[]';
            text = msg.sender.nick_name + '';
            break;
        case 6: // 
            user_level = 99;
            nick_name = '[]';
            text = msg.desc;
            break;
        case 7: // 
            if(msg.sender){
                user_level = msg.sender.user_level;
                nick_name = msg.sender.nick_name;
            } else {
                user_level = 99;
                nick_name = '[]';
            }
            text = msg.desc;
            vueObj.playerEnded = true; // 
            break;
        case 8: // 
            user_level = msg.sender.user_level;
            nick_name = msg.sender.nick_name;
            text = msg.desc;
            break;
        case 9: // 
            user_level = 99;
            nick_name = "[]";
            text = msg.desc;
            break;
        case 10: // 
            user_level = msg.sender.user_level;
            nick_name = msg.sender.nick_name;
            text = msg.text;
            break;
        case 11: // 
            user_level = msg.sender.user_level;
            nick_name = msg.sender.nick_name;
            text = msg.text;
            break;
        case 12: // 
            if (!msg.showMsg) {
                 //return;
            }
            user_level = msg.sender.user_level;
            nick_name = msg.sender.nick_name;
            text = '';

            if(typeof Emotions[msg.imageName] !== 'undefined'){
                text += '<img class="icon" style="height:.7rem;" src="' + Emotions[msg.imageName] + '" />';
            } else {
                text += '[' + msg.imageName + ']';
            }
            break;
        case 17: // 
            user_level = 99;
            nick_name = '[]';
            text = msg.desc;
            break;
        case 20: // 
            user_level = msg.sender.user_level;
            nick_name = msg.sender.nick_name;
            text = msg.text.replace(/\[(face\d+)\]/g, function(word, g1){
                if(typeof Emotions[g1] !== 'undefined'){
                    return '<img class="icon" style="height:.7rem;" src="' + Emotions[g1] + '" />';
                }
                
                return word;
            });
            break;
        case 21: // 
            user_level = msg.sender.user_level;
            nick_name = msg.sender.nick_name;
            text = msg.desc;
            break;
        case 22: // 
            user_level = msg.sender.user_level;
            nick_name = msg.sender.nick_name;
            text = msg.desc;
            break;
        case 23: // 
            user_level = msg.sender.user_level;
            nick_name = msg.sender.nick_name;
            text = msg.conversationDesc + msg.from_msg + '&nbsp;<img class="icon" style="height:2em;" src="' + msg.prop_icon + '" />';
            break;
        case 18: // 
        default:
            return;
    }
    
    if(msg.sender && msg.sender.user_id == loginInfo.identifier) {
        if(msg.sender.user_level > loginInfo.user_level){
            loginInfo.user_level = msg.sender.user_level;
        } else {
            user_level = loginInfo.user_level;
        }
    }

    return {"type": msg.type, "user_level": user_level, "nick_name": nick_name, "text": text, "ext": msg};
    
}

var im_message = {

    /**
     * 
     * @param loginInfo   //sdk
     * @param listeners  //webimlistenersrenmai
     * {loginSuccess,loginError,connectSuccess,disconnect,reconnect,recieveMsg,sendMsgOk,sendMsgFail}
     *
     * listeners
     * 1. loginSuccess 
     * 2. loginError
     * 3. connectSuccess
     * 4. disconnect
     * 5. reconnect
     * 6. recieveMsg: webim.Msg
     * 7. sendMsgOk 
     * 8. sendMsgFail string 
     *
     *
     *
     */
    init: function (loginInfo, listeners) {
        var listeners = this.listeners = {
            loginSuccess: listeners.loginSuccess||function(){},
            loginError: listeners.loginError||function(){},
            connectSuccess: listeners.connectSuccess||function(){},
            disconnect: listeners.disconnect||function(){},
            reconnect: listeners.reconnect||function(){},
            recieveMsg: listeners.recieveMsg||function(){},
            recieveGroupMsg: listeners.recieveGroupMsg||function(){},
            recieveGroupSystem: listeners.recieveGroupSystem||function(){},
            sendMsgOk: listeners.sendMsgOk||function(){},
            sendMsgFail: listeners.sendMsgFail||function(){}
        };

        //
        var options  = {
            'isAccessFormalEnv': true,//
            'isLogOn': false //,
        };

        //IE9()jsonp
        var jsonpCallback = function(rspData) {
            webim.setJsonpLastRspData(rspData);
        };

        //
        var onConnNotify = function(resp){
            switch(resp.ErrorCode){
                case webim.CONNECTION_STATUS.ON:
                    listeners.connectSuccess.call(listeners,resp);
                    break;
                case webim.CONNECTION_STATUS.OFF:
                    listeners.disconnect.call(listeners,resp);
                    break;
                case webim.CONNECTION_STATUS.RECONNECT:
                    listeners.reconnect.call(listeners,resp);
                    break;
                default:
                    //webim.Log.error(': ='+resp.ErrorInfo);
                    break;
            }
        };

        //
        var webim_listeners = {
            "onConnNotify": onConnNotify, //,
            "jsonpCallback": jsonpCallback, //IE9()jsonp
            "onMsgNotify": listeners.recieveMsg, //(())
            "onBigGroupMsgNotify": listeners.recieveGroupMsg, //()
            "onGroupSystemNotifys": listeners.recieveGroupSystem, //
            /* 
            "onGroupInfoChangeNotify": onGroupInfoChangeNotify, //
            "onFriendSystemNotifys": onFriendSystemNotifys, //
             "onProfileSystemNotifys": onProfileSystemNotifys//
             */
        };
        
        webim.login(loginInfo, webim_listeners, options,
            function (resp) {
                listeners.loginSuccess.call(listeners);
            },
            function (err) {
                listeners.loginError.call(listeners,err);
            }
        );
    },

    //
    sendTextMsg:function(loginInfo, to_uid, text, type, is_c2c){
        var type = type || 0;
        if(! text){
            return;
        }
        
        var selSess = this.selSess;
        if(! this.selSess || is_c2c){
            var selType = webim.SESSION_TYPE.C2C; //
            selSess = new webim.Session(selType, to_uid);
        }
        
        var isSend = true;//
        var seq = -1;//-1sdk
        var random = Math.round(Math.random() * 4294967296);//
        var msgTime = Math.round(new Date().getTime() / 1000);//
        var subType = webim.C2C_MSG_SUB_TYPE.COMMON; //
        var from_uid = loginInfo.identifier;
        if(from_uid == '' || from_uid == to_uid){
            return;
        }

        var msg = new webim.Msg(
            selSess, isSend, seq, random,msgTime,from_uid,subType,'',loginInfo.identifierNick
        );

        if(type == 9){
            msg.addCustom(new webim.Msg.Elem.Custom(JSON.stringify({
                "type": type,
                "desc": text,
                "sender": {
                    "user_id": loginInfo.identifier,
                    "nick_name": loginInfo.identifierNick,
                    "user_level": loginInfo.user_level,
                    "head_image": loginInfo.head_image,
                },
            })));
        } else {
            msg.addText(new webim.Msg.Elem.Text(text));
            msg.addCustom(new webim.Msg.Elem.Custom(JSON.stringify({
                "type": type,
                "text": text,
                "sender": {
                    "user_id": loginInfo.identifier,
                    "nick_name": loginInfo.identifierNick,
                    "user_level": loginInfo.user_level,
                    "head_image": loginInfo.head_image,
                },
            })));
        }

        var _this = this;
        webim.sendMsg(msg, function (resp) {
            _this.listeners.sendMsgOk.call(_this.listeners, msg);
        }, function (err) {
            var error_str= err.SrcErrorInfo.replace(/secure check error! beat word/, "");
            _this.listeners.sendMsgFail.call(_this.listeners,error_str);
        });
    },

    //
    recieveMsg: function(to_uid, recieveMsg){
        var options = {
            'Peer_Account': to_uid.toString(), //
            'MaxCnt': 15, //
            'LastMsgTime': 0, //
            'MsgKey': '',
        };
        this.selToID = to_uid;
        webim.getC2CHistoryMsgs(options, function (resp) {
            
            if(recieveMsg){
                resp.MsgList.sort(function(a, b) { 
                    return a.getTime() < b.getTime() ? -1 : 1;
                });

                recieveMsg(resp.MsgList);
            }
        }, function(resp){
            console.log(resp);
        });
    },

    //
    recieveSession: function(recieveMsg){
        webim.syncMsgs(recieveMsg);
    },

    // 
    applyJoinBigGroup: function (loginInfo, groupId) {
        if(! groupId){
            return;
        }

        var options = {
            'GroupId': groupId//id
        };
        var _this = this;
        webim.applyJoinBigGroup(
            options,
            function (resp) {
                //JoinedSuccess:; WaitAdminApproval:
                if (resp.JoinedStatus && resp.JoinedStatus == 'JoinedSuccess') {
                    var selType = webim.SESSION_TYPE.GROUP;
                    var maxLen = webim.MSG_MAX_LENGTH.GROUP;
                    var selSessHeadUrl = 'img/2017.jpg';
                    _this.selSess = new webim.Session(selType, groupId, groupId, selSessHeadUrl, Math.round(new Date().getTime() / 1000));
                    if(typeof loginInfo.identifierNick === 'undefined')
                    {
                        return;
                    }
                    _this.sendTextMsg(loginInfo, groupId, loginInfo.identifierNick + '', 9);
                    webim.Log.info('');
                } else {
                    webim.Log.info('');
                }
            },
            function (err) {
                alert(err.ErrorInfo);
            }
        );
    },

    //
/*     quitBigGroup: function(loginInfo, groupId) {
        if(! groupId){
            return;
        }

        var options = {
            'GroupId': groupId //id
        };
        var _this = this;
        webim.quitBigGroup(
            options,
            function(resp) {
                console.log(resp);
                _this.sendTextMsg(loginInfo, groupId, '' + loginInfo.identifierNick + '...', 6);
                webim.Log.info('');
            },
            function(err) {
                alert(err.ErrorInfo);
            }
        );
    } */
};

export { im_message, convertMsg };


